<template>
    <div>
        <div class="container">
            <div class="row">
                <div class="mr40 mt20">
                    <router-link to="/list-plannogram">
                        <img src="../../assets/icons/back.svg">
                    </router-link>
                </div>
                <div>
                    <h1 class="m0">Update Plannogram</h1>
                    <p class="mt5 mb20 tc-green">Increase the visibility of promotional products in our store highlights</p>
                </div>
            </div>
            <hr class="uline-grey">
            <div class="mt30 row">
                <div class="w20">
                    <label class="form-label">Background Image</label>
                </div>
                <div class="w80">
            <!-- The stylized box for uploading files -->
            <div>
    <!-- Kotak unggah dengan background image -->
    <div class="upload-box" @click="triggerFileInput" :style="backgroundImageStyle">
      <!-- Jika tidak ada gambar yang dipilih, tampilkan ikon dan teks -->
      <div v-if="!selectedImage" class="upload-icon">
        <img src="data:image/png;base64," width="200" height="150">
      </div>
      <div v-if="!selectedImage" class="upload-text"></div>
    </div>
    <!-- Input file yang tersembunyi -->
    <input ref="fileInput" type="file" @change="onFileSelected" style="display: none;">
  </div>
  </div>
            </div>
            <div class="mt50 row">
                <div class="w20">
                    <label class="form-label">Plannogram Name</label>
                </div>
                <div class="w80">
                    <input
                        class="form-text"
                        type="text"
                        placeholder="Masukkan Plannogram Name"
                        maxlength="20"
                    >
                </div>
            </div>
            <div class="mt30 row">
                <div class="w20">
                    <label class="form-label">Broadcast Schedule</label>
                </div>
                <div class="w80">
                    <input
                        class="form-text"
                        type="text"
                        placeholder="Pilih Hari"
                    >
                </div>
            </div>
            <div class="mt30 row">
                <div class="w20">
                    <label class="form-label">Valid From</label>
                </div>
                <div class="w80">
                    <input
                        class="form-text"
                        type="date"
                        placeholder="Input your email"
                    >
                </div>
            </div>
            <div class="mt30 row">
                <div class="w20">
                    <label class="form-label">Valid Until</label>
                </div>
                <div class="w80">
                    <input
                        class="form-text"
                        type="date"
                        placeholder="Input your email"
                    >
                </div>
            </div>
            <div class="mt30 row">
                <div class="w20">
                    <label class="form-label">List Product</label>
                </div>
                <div class="w80">
                    <input
                        class="form-text"
                        type="text"
                        placeholder="Pilih Product"
                    >
                </div>
            </div>
            <div class="mt30 row">
                <div class="w20">
                    <label class="form-label"></label>
                </div>
                <div class="w80">
                    <input
                        class="form-text"
                        type="text"
                        placeholder="Pilih Product"
                    >
                </div>
            </div>
            
            
            
            <div class="flex mt30">
                <router-link to="/list-user">
                    <button class="bt-submit-green">Update Plannogram</button>
                </router-link>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  name: "edit-plannogram",
  data() {
    return {
      selectedImage: null, // Menyimpan gambar yang dipilih
    };
  },
  methods: {
    triggerFileInput() {
      this.$refs.fileInput.click(); // Memicu klik pada input file
    },
    onFileSelected(event) {
      // Menangani ketika sebuah file dipilih
      const file = event.target.files[0];
      this.selectedImage = file;
      this.previewImage(); // Tampilkan pratinjau gambar
    },
    previewImage() {
      // Metode untuk membuat pratinjau gambar
      if (this.selectedImage) {
        const reader = new FileReader();
        reader.onload = (e) => {
          this.backgroundImageStyle = `background-image: url('${e.target.result}')`;
        };
        reader.readAsDataURL(this.selectedImage);
      }
    },
  },
  computed: {
    backgroundImageStyle() {
      // Mengembalikan style yang sesuai jika ada gambar yang dipilih
      if (this.selectedImage) {
        return {
          'background-image': `url('${URL.createObjectURL(this.selectedImage)}')`,
          'background-size': 'cover',
          'background-position': 'center',
        };
      }
      return {}; // Kembali ke style default jika tidak ada gambar
    },
  },
};
</script>

<style>
.upload-box {
  /* Gaya untuk kotak unggah */
  width: 200px; /* Atau ukuran spesifik yang Anda inginkan */
  height: 150px; /* Atau tinggi spesifik yang Anda inginkan */
  background-size: cover;
  background-position: center;
  cursor: pointer;
  border: 2px solid grey;
  /* Tambahkan gaya lain sesuai kebutuhan */
}
</style>


